@import "../../assets/scss/common.scss";

.setting {
  @include fixed($top: 41px, $bottom: 0, $right: -360px, $z-index: 888);
  width: 360px;
  transition: all 0.35s ease-in-out;
  background-color: rgb(21, 21, 20);
  font-size: 14px;
  padding: 24px;
  box-sizing: border-box;
}

.body {
  width: 100%;
  height: 100%;
  overflow: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

.putAway {
  @include flex(column, center, center);
  width: 22px;
  height: 88px;
  position: absolute;
  background-color: rgb(21, 21, 20);
  left: -22px;
  top: 50%;
  margin-top: -44px;
  color: #bcc8d4;
}

.item {
  @include flex(row, center, flex-start);
  color: #bcc8d4;
  height: 48px;

  label {
    flex-basis: 90px;
    text-align: right;
    padding-right: 10px;
  }

  textarea {
    height: 100%;
  }

  input,
  textarea {
    flex: 1;
    box-sizing: border-box;
    margin: 0;
    font-variant: tabular-nums;
    list-style: none;
    font-feature-settings: "tnum";
    position: relative;
    display: inline-block;
    min-width: 0;
    padding: 3px 7px;
    color: rgba(0, 0, 0, 0.85);
    font-size: 12px;
    line-height: 1.66667;
    background-color: #fff;
    background-image: none;
    border: 1px solid #e0e0e0;
    border-radius: 4px;
    transition: all 0.3s;

    &:focus {
      outline: none;
    }
  }
}
