@import "normalize.css";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

div {
  box-sizing: border-box;
}

:global(.react-resizable) {
  position: relative;
}

:global(.react-draggable-active) {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

:global(.react-resizable-handle, .react-draggable) {
  position: absolute;
  width: 20px;
  height: 20px;
  background: url("data:image/svg+xml;base64,");
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}

.react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  background: url("data:image/svg+xml;base64,");
  background-position: bottom right;
  background-size: 6px 6px;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
  bottom: 0;
  right: 0;
}

.react-resizable-handle.react-draggable {
  position: absolute;
  width: 20px;
  height: 20px;
  background: url(data:image/svg+xml;base64,);
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}

.wedget-title {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
}

.wedget-subtitle {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;

  h3,
  h5 {
    margin: 0;
    padding: 0;
  }

  h3 {
    display: flex;
    flex-flow: row nowrap;
    align-items: baseline;
    font-family: DINPro-Medium;
    font-size: 24px;
    color: #ffffff;
    text-align: right;
    line-height: 19px;
    font-weight: 500;
  }

  h3 span {
    font-family: PingFangSC-Medium;
    font-size: 8px;
    color: #ffffff;
    font-weight: 500;
  }

  h5 {
    font-family: PingFangSC-Regular;
    font-size: 8px;
    color: #ffffff;
    text-align: center;
    font-weight: 400;
  }
}

.square {
  overflow: hidden;

  span {
    background: rgba(3, 227, 240, 0.2);
    font-family: PingFangSC-Medium;
    font-size: 10px;
    color: #ffffff;
    text-shadow: 0 1px 15px #03e3f0;
    font-weight: 500;
    height: 20px;
    padding: 0 16px;
    position: absolute;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;

    &.right {
      right: 0;
      top: 0;
      border-radius: 0 0 0px 4px;
    }

    &.left {
      left: 0;
      top: 0;
      border-radius: 0 0 4px 0;
    }
  }
}

.square-table {
  overflow: hidden;

  .title {
    background: rgba(3, 227, 240, 0.2);
    border-radius: 4px 0 4px 0;
    font-family: PingFangSC-Medium;
    font-size: 10px;
    color: #ffffff;
    text-shadow: 0 1px 15px #03e3f0;
    font-weight: 500;
    height: 20px;
    padding: 0 16px;
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
  }

  ul,
  li {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
  }

  ul {
    padding-top: 24px;
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: flex-start;
  }

  li {
    width: 33.3333%;
    height: 22px;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
    border: 1px solid rgba(236, 255, 255, 0.1);
    border-top: none;
    border-left: none;
    box-sizing: border-box;

    span {
      font-family: PingFangSC-Regular;
      font-size: 7px;
      color: rgba(255, 255, 255, 0.85);
      letter-spacing: 0;
      line-height: 22px;
      font-weight: 400;

      &:nth-of-type(1) {
        flex-basis: 20px;
        text-align: center;
        color: #03e3f0;
      }

      &:nth-of-type(2) {
        flex-basis: 30px;
        text-align: left;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }

      &:nth-of-type(3) {
        flex: 1;
        text-align: left;
      }
    }

    &:nth-of-type(n + 4) span:nth-of-type(1) {
      color: rgba(255, 255, 255, 0.85);
    }

    &:nth-of-type(-n + 3) {
      background: rgba(236, 255, 255, 0.1);
    }
  }
}

.square-list {
  overflow: hidden;

  .title {
    background: rgba(3, 227, 240, 0.2);
    border-radius: 4px 0 4px 0;
    font-family: PingFangSC-Medium;
    font-size: 10px;
    color: #ffffff;
    text-shadow: 0 1px 15px #03e3f0;
    font-weight: 500;
    height: 20px;
    padding: 0 16px;
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
  }

  ul,
  li {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
  }

  ul {
    padding-top: 24px;
    flex-flow: column nowrap;
    align-items: flex-start;
    justify-content: flex-start;
  }

  li {
    height: 22px;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;

    span {
      font-family: PingFangSC-Regular;
      font-size: 10px;
      color: rgba(255, 255, 255, 0.85);
      letter-spacing: 0;
      line-height: 22px;
      font-weight: 400;

      &:nth-of-type(1) {
        flex-basis: 30px;
        text-align: center;
        color: #03e3f0;
      }

      &:nth-of-type(2) {
        flex-basis: 50px;
        text-align: left;
      }

      &:nth-of-type(3) {
        flex: 1;
        text-align: left;
      }
    }

    &:nth-of-type(n + 4) span:nth-of-type(1) {
      color: rgba(255, 255, 255, 0.85);
    }

    &:nth-of-type(odd) {
      background: rgba(236, 255, 255, 0.1);
    }
  }
}

.button {
  --text: #fff;
  --primary: #292c3b;
  --check: #275efe;
  --background: #fff;
  --border-radius: 2px;
  --background: #292c3b;
  --background-hover: #292c3b;
  --background-progress: #999eb4;
  --background-progress-active: #275efe;
  --background-progress-success: #292c3b;
  --text: #fff;
  --success-text: #292c3b;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border: 0;
  outline: none;
  padding: 0 15px;
  height: 32px;
  background: var(--primary);
  border-radius: var(--border-radius);
  color: var(--text);
  cursor: pointer;
  box-shadow: 0 10px 30px rgba(#414856, 0.05);
  transition: transform 0.1s linear;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;

  .icons {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 3px 0 0 15px;

    .arrow {
      height: 12px;
      width: 14px;
      stroke-width: 2px;
      stroke-linecap: round;
      stroke-linejoin: round;
      stroke: var(--text);
      fill: none;
      transform: rotate(var(--r, 0deg)) translateY(var(--y, 0px));
      transform-origin: var(--transOrigin, bottom right);
    }

    .line {
      margin-top: 1px;
      height: 4px;
      width: 12px;
      stroke-width: 2px;
      stroke-linecap: round;
      stroke-linejoin: round;
      stroke: var(--text);
      fill: none;
      stroke-dashoffset: var(--sd, 0);
      stroke-dasharray: 16 32;
      --sd: 0;
      transform: rotate(var(--r, 0deg)) translate(var(--x, 0px), var(--y, 0px));
    }

    .check {
      position: absolute;
      height: 13px;
      width: 17px;
      stroke-width: 2px;
      stroke-linecap: round;
      stroke-linejoin: round;
      stroke: var(--check);
      fill: none;
      stroke-dashoffset: var(--sd, 32);
      stroke-dasharray: 18 36;
      --sd: 32;
      transform: translateY(-2px);
    }
  }

  > .text {
    display: block;
    position: relative;
    overflow: hidden;
    font: normal 13px "Lato", "Lucida Grande", "Lucida Sans Unicode", Tahoma,
      Sans-Serif;
    letter-spacing: 0.7px;

    > span {
      display: block;
      transition: transform 0.3s ease, opacity 0.3s ease;

      &::before {
        content: attr(data-text);
        display: block;
        position: absolute;
        left: 0;
        bottom: 100%;
      }
    }
  }

  &:active {
    transform: scale(0.97);
  }

  &.active {
    > .text {
      > span {
        transform: translateY(100%);
        transition: transform 0.3s ease 1.9s, opacity 0.3s ease 1.9s;
      }
    }
  }
}
