@import "./common.scss";

.container {
  @include flex(column, stretch);
  width: 100%;
  height: 100%;
}

.sider {
  @include flex(column, stretch, space-between);
  flex-basis: 40px;
  position: relative;
  background-color: rgb(36, 38, 47);
  box-shadow: rgba(0, 21, 41, 0.12) 2px 0px 6px 0px;
  transition: all 0.35s ease-in-out;

  &.unflod {
    flex-basis: 180px;
  }
}

.menus {
  @include flex(row, flex-start);
  height: 100%;
  position: relative;

  ul {
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
    height: calc(100vh - 81px);
    background-color: #24262f;

    li {
      @include flex(row, center, center);
      width: 40px;
      height: 40px;
      box-sizing: border-box;
      transition: all 0.3s ease-in-out;

      &.active,
      &:hover {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }
  }
}

.charts {
  @include flex(column, stretch, flex-start);
  height: calc(100vh - 81px);
  width: 140px;
  overflow-y: scroll;
  transition: all 0.3s ease-in-out;
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  right: 0;
  top: 0;

  &::-webkit-scrollbar {
    display: none;
  }

  .item {
    margin: 8px 5px 0 5px;
    padding: 3px;
    // background-color: rgb(29, 30, 31);
    border: 1px solid #232630;
    transition: all 0.3s ease-in-out;
    position: relative;
    border-radius: 2px;

    img {
      width: 100%;
      height: 100%;
      visibility: hidden;
    }

    div {
      position: absolute;
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      z-index: 6;
      background-size: contain;
      background-position: center center;
      background-repeat: no-repeat;
    }

    &:hover {
      border-color: rgba(255, 255, 255, 0.5);
    }

    &:last-of-type {
      margin-bottom: 8px;
    }
  }
}

.flod {
  @include flex(column, center, center);
  height: 40px;

  &:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
}

.content {
  @include flex(row, stretch);
  flex: 1;
  background-color: #232630;
}

.tool {
  @include flex(row, center, space-between);
  flex-basis: 40px;
  background-color: rgb(36, 38, 47);
  box-shadow: rgba(0, 21, 41, 0.12) 2px 0px 6px 0px;
  border-bottom: 1px solid #0d0e10;

  span {
    &.logo {
      height: 22px;
      width: 100px;
      margin: 0 0 0 10px;
      display: block;
      background: url("../image/logo.jpg") left center no-repeat;
      background-size: contain;
    }

    &.tips {
      color: #fff;
      font-size: 13px;
      flex: 1;
    }

    &.code {
      @include flex(row, center, center);
      width: 40px;
      height: 40px;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
        background-color: rgba(0, 0, 0, 0.2);
      }
    }
  }
}

.edit {
  flex: 1;
  position: relative;
  border-radius: 2px;
  background-color: #232630;
  overflow: hidden;
}
