@use "sass:meta";

$PingFang-SC: PingFang SC;
$PingFangSC-Medium: PingFangSC-Medium;
$PingFangSC-Regular: PingFangSC-Regular;
$PingFangSC-Semibold: PingFangSC-Semibold;

@mixin flex($direction, $items: center, $content: flex-start, $wrap: nowrap) {
  display: flex;
  flex-wrap: $wrap;
  align-items: $items;
  flex-direction: $direction;
  justify-content: $content;
}

.text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@mixin text-ellipsis-multiline($line-clamp: 2) {
  -o-text-overflow: -o-ellipsis-lastline;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: $line-clamp; // 行数
  -webkit-box-orient: vertical;
  word-break: break-all;
  white-space: normal;
}

@mixin meta($args...) {
  @each $position, $num in meta.keywords($args) {
    #{$position}: $num;
  }
}

@mixin fixed($args...) {
  position: fixed;
  @include meta($args...);
}

// https://github.com/frozenui/frozenui/blob/master/sass/base/mixin.scss
@mixin border-radius($border-color, $border-radius, $border-before-radius) {
  border: 1px solid $border-color;
  border-radius: $border-radius;

  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    position: relative;
    border: 0;

    &:before {
      content: "";
      width: 200%;
      height: 200%;
      position: absolute;
      top: 0;
      left: 0;
      border: 1px solid $border-color;
      transform: scale(0.5);
      transform-origin: 0 0;
      padding: 1px;
      box-sizing: border-box;
      border-radius: $border-before-radius;
      pointer-events: none;
    }
  }
}

.skeleton {
  border-radius: 3px;
  background: rgba(130, 130, 130, 0.2);
  background: -webkit-gradient(
    linear,
    left top,
    right top,
    color-stop(8%, rgba(130, 130, 130, 0.2)),
    color-stop(18%, rgba(130, 130, 130, 0.3)),
    color-stop(33%, rgba(130, 130, 130, 0.2))
  );
  background: linear-gradient(
    to right,
    rgba(130, 130, 130, 0.2) 8%,
    rgba(130, 130, 130, 0.3) 18%,
    rgba(130, 130, 130, 0.2) 33%
  );
  background-size: 800px 100px;
  animation: wave-lines 1.5s infinite ease-out;
}

@keyframes wave-lines {
  0% {
    background-position: -468px 0;
  }

  100% {
    background-position: 468px 0;
  }
}

.page {
  @include flex(column, stretch, flex-start);
  min-width: 100vw;
  min-height: 100vh;
  background: #fff;
}

.back {
  width: 100%;
  height: 45px;
  display: inline-block;
  background: url("//sr.ffquan.cn/dtk_zhonghe/20210122/c056ts76vrk9m9uccsjg0.png")
    center center / 20px 20px no-repeat;
}
