@import "../../assets/scss/common.scss";

.menu {
  background-color: #2e2e34;
  padding: 8px 0;
  border-radius: 2px;
  position: absolute;
  z-index: 9999;
  max-height: none;
  min-width: 200px;
  text-align: left;
}

.item {
  @include flex(row, center, space-between);
  height: 24px;
  font-size: 12px;
  color: #fff;
  cursor: default;
  padding: 0 16px;

  &:hover {
    background-color: #3e8ae2;
  }
}
