@import "../../assets/scss/common.scss";

.body {
  height: 100%;
  width: 100%;
  background-color: rgb(45, 48, 57);

  .nav {
    font-family: initial;
    line-height: initial;
    font-size: initial;
    font-weight: initial;
    background: #1e1f26;
    height: 50px;
    -webkit-box-shadow: 0 1px 3px rgb(0 0 0 / 30%);
    box-shadow: 0 1px 3px rgb(0 0 0 / 30%);
    padding-left: 8px;
    position: relative;
    white-space: nowrap;
    z-index: 2;

    ul {
      white-space: nowrap;
      z-index: 1;
      list-style: none;
      display: inline-block;
      padding: 0;
      margin: 0;
      margin-top: 7px;
      font-size: 0;

      li {
        cursor: pointer;
        display: inline-block;
        margin-right: 1px;
        font-family: "Lato", "Lucida Grande", "Lucida Sans Unicode", Tahoma,
          Sans-Serif;
        display: inline-block;
        -webkit-transition: background 200ms ease, -webkit-box-shadow 200ms ease;
        transition: background 200ms ease, -webkit-box-shadow 200ms ease;
        transition: background 200ms ease, box-shadow 200ms ease;
        transition: background 200ms ease, box-shadow 200ms ease,
          -webkit-box-shadow 200ms ease;
        text-decoration: none;
        padding: 10px 16px;
        letter-spacing: 0.6px;
        font-size: 13px;
        -webkit-box-shadow: inset 0 3px transparent;
        box-shadow: inset 0 3px transparent;
        color: #e3e4e8;
        background-color: #444857;

        &:active,
        &:focus {
          transform: translateY(3px);
          transition: all 0.45s ease-in-out;
        }

        &.active {
          background: #5a5f73;
          color: #e3e4e8;
          box-shadow: inset 0px 3px #868ca0;
        }

        &:first-child {
          border-top-left-radius: 2px;
          border-bottom-left-radius: 2px;
        }

        &:last-child {
          border-top-right-radius: 2px;
          border-bottom-right-radius: 2px;
        }
      }
    }
  }

  .content {
    width: 100%;
    height: calc(100% - 50px);
    overflow: hidden;

    ul,
    li {
      list-style: none;
      margin: 0;
      padding: 0;
      height: 100%;
    }

    ul {
      width: 400%;
      transition: all 0.35s ease-in-out;
      display: flex;
      flex-flow: row nowrap;
    }

    li {
      display: inline-block;
      flex: 1;
      overflow-y: scroll;
      white-space: "pre-line";
      // background-color: rgb(19, 20, 23);
      background-color: rgb(29, 31, 33);
    }
  }
}
