@import "../../assets/scss/common.scss";

.ruler {
  height: 100%;
  background-color: rgb(24, 26, 35);
  position: relative;
}

.aided {
  @include flex(row, center, center);
  top: 0;
  left: 0;
  width: 23px;
  height: 20px;
  position: absolute;
}

.abscissa {
  top: 0;
  left: 20px;
  position: absolute;
}

.ordinate {
  top: 20px;
  left: 0;
  position: absolute;
}

.container {
  position: absolute;
  top: 20px;
  left: 20px;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(
      45deg,
      #404040 25%,
      transparent 0,
      transparent 75%,
      #404040 0
    ),
    linear-gradient(
      45deg,
      #404040 25%,
      transparent 0,
      transparent 75%,
      #404040 0
    );
  background-color: #4a4a4a;
  background-size: 10px 10px;
  background-position: 0 0, 5px 5px;
}

.backdrop {
  transform-origin: "0 0";
  transition: all 0.35s ease-in-out;
  position: relative;
}

.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  background: transparent;
}

.item {
  @include flex(row, center, center);
  color: #fff;
  font-size: 10px;
  position: absolute;
  box-sizing: border-box;
}

.box {
  top: 0;
  left: 0;
  position: absolute;
  text-align: center;
  box-sizing: border-box;
}

.box .text {
  text-align: center;
}

.guides {
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  position: absolute;
  pointer-events: none;
}

.line {
  @include flex(row, flex-end, flex-end);
  position: absolute;
  top: 0;
  left: 0;
  border: 1px dashed #808080;
  border-top: none;
  border-left: none;
  color: #fff;
  text-align: right;
  z-index: 999;

  span {
    color: #808080;
    font-size: 12px;
    line-height: 30px;
    padding-right: 10px;
  }
}

.spacing {
  @include flex(row, center, center);
  position: absolute;
  background-color: rgb(236, 98, 43);
  z-index: 1000;

  span {
    color: #808080;
    font-size: 12px;
    line-height: 20px;
    position: absolute;
  }

  &::after,
  &::before {
    content: "";
    width: 1px;
    height: 8px;
    background-color: rgb(236, 98, 43);
    position: absolute;
  }

  &.vertical {
    &::after {
      top: -4px;
      transform: rotate(55deg);
    }

    &::before {
      bottom: -4px;
      transform: rotate(55deg);
    }
  }

  &.horizon {
    &::after {
      left: -1px;
      transform: rotate(25deg);
    }

    &::before {
      right: -1px;
      transform: rotate(25deg);
    }
  }
}

.checked {
  position: absolute;
  border: 1px solid transparent;
  box-sizing: border-box;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;

  span {
    display: none;
  }

  &.cur {
    border: 1px solid rgb(72, 165, 241);

    span {
      display: block;
    }

    div {
      &.dots {
        width: 6px;
        height: 6px;
        display: block;
        background: rgb(72, 165, 241);
        border-radius: 50%;
        position: absolute;

        &:nth-of-type(1) {
          top: -3px;
          left: -3px;
        }

        &:nth-of-type(2) {
          top: -3px;
          left: 50%;
          margin-left: -3px;
        }

        &:nth-of-type(3) {
          top: -3px;
          right: -3px;
        }

        &:nth-of-type(4) {
          right: -3px;
          top: 50%;
          margin-top: -3px;
        }

        &:nth-of-type(5) {
          bottom: -3px;
          right: -3px;
        }

        &:nth-of-type(6) {
          bottom: -3px;
          left: 50%;
          margin-left: -3px;
        }

        &:nth-of-type(7) {
          bottom: -3px;
          left: -3px;
        }

        &:nth-of-type(8) {
          left: -3px;
          top: 50%;
          margin-top: -3px;
        }
      }
    }
  }
}
