@import "../../assets/scss/common.scss";

.mask {
  @include fixed($top: 0, $bottom: 0, $right: 0, $left: 0, $z-index: 998);
  opacity: 1;
  background-color: #00000073;
  animation: antdDrawerFadeIn 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  pointer-events: auto;
}

@keyframes antdDrawerFadeIn {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.drawer {
  @include fixed($top: 0, $bottom: 0, $right: -80vw, $z-index: 999);
  height: 100vh;
  width: 80vw;
  transition: all 0.35s ease-in-out;
  background-color: #fff;
  font-size: 14px;
}

.aided {
  @include flex(row, center, center);
  top: 0;
  left: 0;
  width: 23px;
  height: 20px;
  position: absolute;
}
